/* css wave */
/* @import url(//fonts.googleapis.com/css?family=Lato:300:400); */

.leo_playpage_wave h1 {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  letter-spacing: 2px;
  font-size: 48px;
}
.leo_playpage_wave p {
  font-family: 'Lato', sans-serif;
  letter-spacing: 1px;
  font-size: 14px;
  color: #333333;
}

.leo_playpage_wave .header {
  position: relative;
  text-align: center;
  background: linear-gradient(
    60deg,
    rgba(84, 58, 183, 1) 0%,
    rgba(0, 172, 193, 1) 100%
  );
  color: white;
}
.leo_playpage_wave .logo {
  width: 50px;
  fill: white;
  padding-right: 15px;
  display: inline-block;
  vertical-align: middle;
}

.leo_playpage_wave .inner-header {
  height: 65vh;
  width: 100%;
  margin: 0;
  padding: 0;
}

.leo_playpage_wave .flex {
  /*Flexbox for containers*/
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.leo_playpage_wave .waves {
  position: relative;
  width: 100%;
  height: 15vh;
  margin-bottom: -7px; /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}

.leo_playpage_wave .content {
  position: relative;
  height: 20vh;
  text-align: center;
  background-color: white;
  background-color: rgb(84, 84, 84);
}

/* Animation */

.leo_playpage_wave .parallax > use {
  animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.leo_playpage_wave .parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.leo_playpage_wave .parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.leo_playpage_wave .parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.leo_playpage_wave .parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}
