#leo_audiopage ol,
#leo_audiopage ul,
.leo_container ol,
#leo_player ol,
.leo_container ul,
#leo_player ul,
.leo_playpage_musiclist ul,
.leo_playpage_musiclistli {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  list-style: none;
}
.leo_container blockquote,
.leo_container q {
  quotes: none;
}
#leo_audiopage blockquote:after,
#leo_audiopage blockquote:before,
.leo_container blockquote:after,
.leo_container blockquote:before,
.leo_container q:after,
.leo_container q:before {
  content: '';
  content: none;
}
.leo_container table {
  border-collapse: collapse;
  border-spacing: 0;
}
.leo_container *,
#leo_player * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.leo_container a,
#leo_player a,
#leo_audiopage a {
  text-decoration: none;
}
.leo_container img,
#leo_player img,
.leo_playpage_musiclist img,
#leo_audiopage img {
  max-width: 100%;
  height: auto;
}
