.leo_playpage_main main {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  max-width: 950px;
}

.leo_playpage_main .player {
  position: relative;
  width: 60%;
  background-color: #f4f3f2;
  background: repeating-linear-gradient(
    90deg,
    #e8e6e4,
    #e8e6e4 1px,
    #f4f3f2 1px,
    #f4f3f2 10%
  );
  border: 1px solid #aeaeae;
  border-radius: 25%;
  box-shadow: inset white 0 0 2px 1px, inset rgba(0, 0, 0, 0.06) 0 0 48px,
    rgba(0, 0, 0, 0.2) 0 15px 30px -10px;
}
.leo_playpage_main .player:after {
  content: '';
  display: block;
  padding-bottom: 100%;
}
.leo_playpage_main .player:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  background: repeating-linear-gradient(
    -45deg,
    #efeeec,
    #efeeec 1px,
    transparent 1px,
    transparent 4px
  );
  border-radius: 25%;
}

.leo_playpage_main .record {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 70%;
  height: 70%;
  background-color: #363636;
  background: linear-gradient(
    to right,
    #363636 0%,
    #666666 40%,
    #616161 60%,
    #1f1e1f 100%
  );
  border-radius: 100%;
  box-shadow: inset #030303 0 0 0 1px, inset rgba(255, 255, 255, 0.3) 0 0 0 2px;
  -webkit-transform: rotate(0) translateZ(0);
          transform: rotate(0) translateZ(0);
}
.leo_playpage_main .record:before,
.leo_playpage_main .record:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;

  background: linear-gradient(
    to right,
    #363636 0%,
    #666666 41%,
    #616161 61%,
    #1f1e1f 100%
  );
  border-radius: 100%;
}
.leo_playpage_main .record:before {
  width: 88%;
  height: 88%;
  /* background-image: url(../../pages/audio/image/62d669741d787b91e6fc027ec9842a2d.jpeg); */
