  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 15%;
  height: 15%;
  padding: 4.5% 6%;
  background-color: #ff4e5b;
  background: repeating-linear-gradient(
    -45deg,
    #ff3543,
    #ff3543 1px,
    #ff4e5b 1px,
    #ff4e5b 3px
  );
