.leo_playpage_main .record .label:before,
.leo_playpage_main .record .label:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  border-radius: 100%;
  z-index: -1;
