.leo_playpage_main .record .label:before {
  width: 35%;
  height: 35%;
  background-color: #ff2f3f;
}
.leo_playpage_main .record .label:after {
  width: 30%;
  height: 30%;
  background: repeating-linear-gradient(
    -45deg,
    #ff3543,
    #ff3543 1px,
    #ff4e5b 1px,
    #ff4e5b 3px
  );
