* {
  box-sizing: border-box;
}

.leo_playpage_main main {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  max-width: 950px;
}

.leo_playpage_main .player {
  position: relative;
  width: 60%;
  background-color: #f4f3f2;
  background: repeating-linear-gradient(
    90deg,
    #e8e6e4,
    #e8e6e4 1px,
    #f4f3f2 1px,
    #f4f3f2 10%
  );
  border: 1px solid #aeaeae;
  border-radius: 25%;
  box-shadow: inset white 0 0 2px 1px, inset rgba(0, 0, 0, 0.06) 0 0 48px,
    rgba(0, 0, 0, 0.2) 0 15px 30px -10px;
}
.leo_playpage_main .player:after {
  content: '';
  display: block;
  padding-bottom: 100%;
}
.leo_playpage_main .player:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  background: repeating-linear-gradient(
    -45deg,
    #efeeec,
    #efeeec 1px,
    transparent 1px,
    transparent 4px
  );
  border-radius: 25%;
}

.leo_playpage_main .record {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 70%;
  height: 70%;
  background-color: #363636;
  background: linear-gradient(
    to right,
    #363636 0%,
    #666666 40%,
    #616161 60%,
    #1f1e1f 100%
  );
  border-radius: 100%;
  box-shadow: inset #030303 0 0 0 1px, inset rgba(255, 255, 255, 0.3) 0 0 0 2px;
  transform: rotate(0) translateZ(0);
}
.leo_playpage_main .record:before,
.leo_playpage_main .record:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;

  background: linear-gradient(
    to right,
    #363636 0%,
    #666666 41%,
    #616161 61%,
    #1f1e1f 100%
  );
  border-radius: 100%;
}
.leo_playpage_main .record:before {
  width: 88%;
  height: 88%;
  /* background-image: url(../../pages/audio/image/62d669741d787b91e6fc027ec9842a2d.jpeg); */
  background-image: var(--songImg-url);
  background-size: cover;
  background-color: #505050;
  box-shadow: inset #918585 0 0 0 1px, inset rgba(255, 255, 255, 0.4) 0 0 0 2px,
    inset #292929 0 0 0 3px;
}
.leo_playpage_main .record:after {
  width: 20%;
  height: 20%;
  background-color: #363636;
  box-shadow: inset rgba(0, 0, 0, 0.2) 0 0 0 1px, rgba(0, 0, 0, 0.1) 0 0 2px 3px;
  transform: rotate(5deg);
}

.leo_playpage_main .record .label {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 15%;
  height: 15%;
  padding: 4.5% 6%;
  background-color: #ff4e5b;
  background: repeating-linear-gradient(
    -45deg,
    #ff3543,
    #ff3543 1px,
    #ff4e5b 1px,
    #ff4e5b 3px
  );
  text-align: center;
  border-radius: 100%;
  z-index: 2;
}
.leo_playpage_main .record .label:before,
.leo_playpage_main .record .label:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  border-radius: 100%;
  z-index: -1;
}
.leo_playpage_main .record .label:before {
  width: 35%;
  height: 35%;
  background-color: #ff2f3f;
}
.leo_playpage_main .record .label:after {
  width: 30%;
  height: 30%;
  background: repeating-linear-gradient(
    -45deg,
    #ff3543,
    #ff3543 1px,
    #ff4e5b 1px,
    #ff4e5b 3px
  );
}
.leo_playpage_main .record .label h2 {
  font-weight: bold;
}
.leo_playpage_main .record .label h2,
.leo_playpage_main .record .label h3 {
  color: #810009;
  z-index: 4;
}
.leo_playpage_main .record .label p {
  position: absolute;
  top: 50%;
  right: 10%;
  bottom: 0;
  margin: auto;
}

.leo_playpage_main .spindle {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 25%;
  height: 25%;
  background-color: black;
  border-radius: 100%;
  box-shadow: rgba(0, 0, 0, 0.1) 0 0 12px 2px;
  transform: scale(0.2);
  z-index: 3;
}
.leo_playpage_main .spindle:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 70%;
  height: 70%;
  background-color: #ededed;
  background: linear-gradient(
    to right,
    #e2e2e2 0%,
    #d1d1d1 51%,
    #dbdbdb 51%,
    #fefefe 100%
  );
  border-radius: 100%;
  z-index: 4;
}

.leo_playpage_main .arm-container {
  position: absolute;
  width: 30%;
  height: 5%;
  top: 15%;
  right: 19%;
  transform-origin: 94.2%;
  transition: 2s transform linear;
}
.leo_playpage_main .arm-container.playing {
  transform: rotate(-47deg);
}
.leo_playpage_main .arm-container.pause {
  transform: rotate(-120deg);
}

.leo_playpage_main .arm {
  position: absolute;
  top: 0;
  right: -25%;
  width: 100%;
  height: 100%;
  background-color: #ededed;
  background: linear-gradient(to right, #f2f2f2 0%, #fefefe 100%);
  border: 1px solid #e3e3e3;
  border-radius: 12px;
  box-shadow: inset white 0 -1px 2px, inset rgba(0, 0, 0, 0.1) -2px -4px 12px,
    rgba(0, 0, 0, 0.06) -4px 4px 6px;
  z-index: 2;
}

.leo_playpage_main .knob {
  position: absolute;
  background-color: #ededed;
  border-radius: 100%;
  transform: translateZ(0);
  z-index: 2;
}
.leo_playpage_main .knob:after {
  content: '';
  display: block;
  padding-bottom: 100%;
}
.leo_playpage_main .knob.bottom {
  background-color: #dedede;
  border: 1px solid #d9d9d9;
  border-radius: 100%;
}
.leo_playpage_main .knob.top {
  background: linear-gradient(to right, #f2f2f2 0%, #fefefe 100%);
  border: 1px solid #e3e3e3;
  box-shadow: inset white 0 -1px 2px, inset rgba(0, 0, 0, 0.06) -6px -6px 18px,
    rgba(0, 0, 0, 0.06) -4px 4px 6px, rgba(0, 0, 0, 0.15) 2px 2px 6px;
}

.leo_playpage_main .weight.bottom {
  top: -61%;
  right: -13.5%;
  width: 38%;
}
.leo_playpage_main .weight.top {
  width: 30%;
  top: -40%;
  right: -9.5%;
}
.leo_playpage_main .weight.top:before {
  content: '';
  position: absolute;
  top: 34%;
  right: 245%;
  width: 20%;
  height: 20%;
  background-color: #ff6873;
  border: 1px solid #ff4452;
  border-radius: 100%;
  box-shadow: inset rgba(255, 255, 255, 0.6) 2px 2px 4px;
  transform: translateZ(0);
  z-index: 4;
}

.leo_playpage_main button.play {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: none;
  border-radius: 100%;
  font-family: 'Dosis', sans-serif;
  cursor: pointer;
  outline: none;
  transform: rotate(120deg);
}

.leo_playpage_main .tooltip {
  position: absolute;
  bottom: 150%;
  left: 50%;
  margin-left: -60px;
  padding: 8px 20px;
  width: 120px;
  color: #f4f3f2;
  background: #363636;
  text-transform: uppercase;
  text-align: center;
  border-radius: 2px;
  opacity: 0;
  transform: translateY(-60%);
  z-index: 1;
}
.leo_playpage_main .tooltip:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  top: 100%;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 12px 0 12px;
  border-color: #363636 transparent transparent transparent;
}

.leo_playpage_main .volume.bottom {
  bottom: 13.5%;
  left: 14%;
  width: 11%;
  transform: translateZ(0);
}
.leo_playpage_main .volume.bottom div {
  position: absolute;
  width: 20%;
  height: 5%;
  background-color: #d4d4d4;
  transform: translateZ(0);
}
.leo_playpage_main .volume.bottom div:first-child {
  bottom: 110%;
  right: 80%;
}
.leo_playpage_main .volume.bottom div:last-child {
  top: 85%;
  left: 105%;
}
.leo_playpage_main .volume.bottom div:last-child:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  background-color: inherit;
  transform: rotate(90deg);
}
.leo_playpage_main .volume.top {
  bottom: 14.8%;
  left: 15.3%;
  width: 8.5%;
}
.leo_playpage_main .volume.top:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  bottom: 55%;
  width: 5%;
  height: 25%;
  background-color: #d4d4d4;
  border-radius: 24px;
}

.leo_playpage_main .speaker {
  position: absolute;
  right: 5%;
  bottom: 4%;
  width: 20%;
  transform: scale(0.4);
}
.leo_playpage_main .speaker:after {
  content: '';
  display: block;
  padding-bottom: 100%;
}
.leo_playpage_main .speaker .hole {
  margin-right: 12%;
  margin-bottom: 12%;
  width: 13%;
  float: left;
  background-color: #262626;
  border-radius: 100%;
}
.leo_playpage_main .speaker .hole:after {
  content: '';
  display: block;
  padding-bottom: 100%;
}

.leo_playpage_main footer {
  position: fixed;
  right: 0;
  bottom: 0;
  padding: 12px;
  font-size: 11px;
}
.leo_playpage_main footer a {
  display: inline-block;
  margin-right: 12px;
}
.recordRotate {
  animation: spin 6s linear infinite;
  animation-play-state: running;
}
.stopRecordRotate {
  animation-play-state: paused;
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
